@import url(https://fonts.googleapis.com/css?family=Roboto:400,700,900);
@media screen and (max-width: 580px){.weatherDisplay{flex-direction:column}}*{margin:0;padding:0;box-sizing:border-box;font-family:"Roboto", sans-serif;color:#292929;text-shadow:1px 1px 3px rgba(0,0,0,0.3)}body{margin:0;padding:0;line-height:1;background-color:#def6ed;overflow-x:hidden}footer{position:fixed;bottom:0;height:5%;background:#3cbc8d;width:100%;display:flex;justify-content:center;align-items:center}input:-webkit-autofill,input:-webkit-autofill:hover,input:-webkit-autofill:focus,input:-webkit-autofill:active{box-shadow:0 0 0 30px #3cbc8d inset !important;-webkit-box-shadow:0 0 0 30px #3cbc8d inset !important;-webkit-text-fill-color:rgba(255,255,255,0.8) !important}.form{display:flex;flex-direction:column;width:100vw}.form input{border:none;background-color:#3cbc8d;height:2em;padding:20px;font-size:1em;color:rgba(255,255,255,0.8)}.form input:focus{outline:none;border:2px solid #12573e}.form input::-webkit-input-placeholder{color:rgba(255,255,255,0.6)}.form input:-ms-input-placeholder{color:rgba(255,255,255,0.6)}.form input::-ms-input-placeholder{color:rgba(255,255,255,0.6)}.form input::placeholder{color:rgba(255,255,255,0.6)}.form button{background-color:#8ddbbf;border:none;border-bottom:2px solid #3cbc8d;padding:10px;font-size:1em;font-weight:bold;cursor:pointer}.form button:hover{transition:background-color 0.3s ease-in, border-bottom 0.3s ease-in, color 0.3s ease-in;background-color:#12573e;border-bottom:2px solid #12573e;color:white}.form button:focus{transition:background-color 0.3s ease-in-out, border-bottom 0.3s ease-in-out, color 0.3s ease-in-out;border-bottom:2px solid #12573e;outline:none;margin:0;background-color:#12573e;color:white}.error{background-color:#eb4d4d;color:white;padding:10px;text-align:center}.weatherDisplay{padding:10px;display:flex}.weatherMain{display:flex;flex-direction:column;justify-content:center;align-items:center;flex:1 1}.weatherMain>*{padding:5px}.temperature{display:flex;flex-direction:column;align-items:center;justify-content:center}.temperature>* :not(select){padding:10px}.temp{padding:10px;font-size:3em;font-weight:500}.temp_secondary{font-size:1.3em}.feels_like{padding:10px}.arrow{padding:0 !important}.temp_humidity_wind{flex:1 1;display:flex;flex-direction:column}.temp_humidity_wind .wind,.temp_humidity_wind p{text-align:center;padding:5px;font-weight:bold}.svg{transform:scale(1.5);margin:20px}.tempSelect{width:100%;text-align-last:center;text-align:center;-ms-text-align-last:center;-moz-text-align-last:center;background:#3cbc8d;outline:none;border:none;border:2px solid #12573e;color:white;height:2em;cursor:pointer}.windSelect{width:100%;text-align-last:center;text-align:center;-ms-text-align-last:center;-moz-text-align-last:center;background:#3cbc8d;outline:none;border:none;border:2px solid #12573e;color:white;height:2em;cursor:pointer}

